import React,{useState,useEffect} from "react";
import mylogo from "../../assets/image/n-letter.png"
import myCV from "../../assets/cv/Nanda Hayatun Nufus-Curriculum Vitae.pdf"
import { NavLink } from "react-router-dom";
const HeaderNav=()=>{
    const [activeLink, setActiveLink] = useState('home');
    const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    }

    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }, [])

  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
  }
  
    return(
        <nav className="navbar navbar-expand-lg navbar-mainbg">
            <NavLink className="navbar-brand navbar-logo" to="/" exact> Its me! Nanda
            </NavLink>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <i className="fas fa-bars text-white"></i>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto">
                <div className="hori-selector">
                    <div className="left"></div>
                    <div className="right"></div>
                </div>
                <li className="nav-item active">
                    <NavLink className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('home')} exact>
                    <i className="fas fa-techometer-alt"></i>Home
                    </NavLink>
                </li>
                <li className="nav-item active">
                    <NavLink className={activeLink === 'about' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('about')} exact>
                    <i className="fas fa-techometer-alt"></i>About
                    </NavLink>
                </li>
                <li className="nav-item active">
                    <NavLink className={activeLink === 'skills' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('skills')} exact>
                    <i className="fas fa-techometer-alt"></i>Skills
                    </NavLink>
                </li>
                </ul>
            </div>
        </nav>
    );
}
export default HeaderNav;
